var i, j, len, makeBoth;

Router.configure({
  layoutTemplate: 'layout',
  loadingTemplate: 'loading'
});

Router.route('/', {
  action: function() {
    return this.render('home');
  }
});

Router.route('/struktur', {
  action: function() {
    return this.render('struktur');
  }
});

Router.route('/tupoksi', {
  action: function() {
    return this.render('tupoksi');
  }
});

this.coll = [];

this.schema = [];

this.files = new FS.Collection('files', {
  stores: [new FS.Store.GridFS('filesStore')],
  filter: {
    maxSize: 21234567,
    allow: {
      extensions: ['png', 'jpg', 'pdf']
    }
  }
});

files.allow({
  insert: function() {
    return true;
  },
  update: function() {
    return true;
  },
  remove: function() {
    return true;
  },
  download: function() {
    return true;
  },
  fetch: null
});

makeBoth = function(category) {
  Router.route('/' + category, {
    action: function() {
      return this.render('blog');
    },
    waitOn: function() {
      return Meteor.subscribe(category);
    }
  });
  coll[category] = new Meteor.Collection(category);
  coll[category].attachSchema({
    title: {
      type: String,
      label: 'Judul Data'
    },
    date: {
      type: Date,
      label: 'Tanggal Data'
    },
    text: {
      type: String,
      label: 'Isi Data',
      autoform: {
        type: 'quill'
      }
    },
    files: {
      type: [String],
      label: 'Lampiran',
      optional: true
    },
    'files.$': {
      type: String,
      optional: true,
      autoform: {
        afFieldInput: {
          type: 'fileUpload',
          collection: 'files'
        }
      }
    }
  });
  coll[category].allow({
    insert: function() {
      return true;
    },
    update: function() {
      return true;
    },
    remove: function() {
      return true;
    }
  });
  if (Meteor.isServer) {
    return Meteor.publish(category, function() {
      return coll[category].find({});
    });
  }
};

for (j = 0, len = categories.length; j < len; j++) {
  i = categories[j];
  makeBoth(i.name);
}

Meteor.methods({
  removePage: function(category, pageId) {
    return coll[category].remove({
      _id: pageId
    });
  },
  removeFile: function(fileId) {
    return files.remove({
      _id: fileId
    });
  }
});
